import { FC } from "react";
import classNames from "classnames/bind";

import { HorizontalScroll } from "common/components/HorizontalScroll";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { Text, TextTag } from "common/components/Text";
import { Icon } from "common/components/Icons";
import Step1Icon from "common/components/Icons/icons/step-icon-1.svg";
import Step2Icon from "common/components/Icons/icons/step-icon-2.svg";
import Step3Icon from "common/components/Icons/icons/step-icon-3.svg";
import { Picture } from "common/components/Picture";

import { TeensStepItemProps, TeensStepsProps } from "./TeensSteps.types";
import styles from "./TeensSteps.module.scss";

const cx = classNames.bind(styles);

export const TeensSteps: FC<TeensStepsProps> = ({ title, list, className }) => {
    const renderStepItem = (
        { title, text, img }: TeensStepItemProps,
        index: number
    ) => {
        const id = index + 1;

        const renderIcon = () => {
            switch (id) {
                case 1:
                    return <Step1Icon />;
                case 2:
                    return <Step2Icon />;
                case 3:
                    return <Step3Icon />;
                default:
                    return null;
            }
        };

        return (
            <div className={cx("step")} key={`step-${index}`}>
                <div className={cx("first-row")}>
                    <Icon className={cx("step-icon", `step-icon_${id}`)}>
                        {renderIcon()}
                    </Icon>
                    <Picture className={cx("image")} mobile={img} />
                </div>
                <div className={cx("second-row")}>
                    <span className={cx("step-index")}>{id}.</span>
                    <div className={cx("step-content")}>
                        <Text className={cx("step-title")} tag={TextTag.H4}>
                            {title}
                        </Text>
                        <Text className={cx("step-text")}>{text}</Text>
                    </div>
                </div>
            </div>
        );
    };

    return (
        <section className={cx("root", className)}>
            <Heading className={cx("title")} tag={HeadingTag.H2}>
                {title}
            </Heading>
            <div className={cx("container")}>
                <HorizontalScroll>
                    <div className={cx("content")}>
                        {list.map(renderStepItem)}
                    </div>
                </HorizontalScroll>
            </div>
        </section>
    );
};
