import { FC } from "react";
import classNames from "classnames/bind";

import { Heading, HeadingTag } from "common/components/new/Heading";
import { Picture } from "common/components/Picture";

import { BracketsProps } from "../../Brackets.types";
import styles from "./Experience.module.scss";
import { ExperienceType } from "../../Brackets";

const cx = classNames.bind(styles);

export const Experience: FC<
    {
        handleNextExp: () => void;
        showNextExp: ExperienceType | null;
    } & BracketsProps
> = ({
    firstExperience: {
        title,
        mainPhoto,
        images,
        subtitle,
        tooltip,
        treatmentPlan,
        description,
        titleNextOpinion,
    },
    showNextExp,
    secondExperience,
    handleNextExp,
}) => {
    return (
        <div className={cx("root")}>
            <div className={cx("slide-container", "wrapper-titles")}>
                <div
                    className={cx("slide-track", "slide-track-titles", {
                        slideToRight: showNextExp === ExperienceType.FIRST,
                        slideToLeft: showNextExp === ExperienceType.SECOND,
                    })}
                >
                    <Heading className={cx("title")} tag={HeadingTag.H2}>
                        "{secondExperience?.title}"
                    </Heading>
                    <Heading className={cx("title")} tag={HeadingTag.H2}>
                        "{title}"
                    </Heading>
                </div>
            </div>
            <div className={cx("wrapper-images")}>
                <div className={cx("slide-container-image")}>
                    <div
                        className={cx("slide-track-images", {
                            slideToLeftImages:
                                showNextExp === ExperienceType.FIRST,
                            slideToRightImages:
                                showNextExp === ExperienceType.SECOND,
                        })}
                    >
                        <div className={cx("slide-image")}>
                            <Picture
                                mobile={{
                                    jpeg: mainPhoto.mobile.jpeg,
                                    webp: mainPhoto.mobile.webp,
                                }}
                                desktop={{
                                    jpeg: mainPhoto.desktop.jpeg,
                                    webp: mainPhoto.desktop.webp,
                                    media: "(min-width: 1024px)",
                                }}
                                alt="client"
                                classNameImg={cx(
                                    "main-photo",
                                    "main-photo_small"
                                )}
                            />
                        </div>
                        <div className={cx("slide-image")}>
                            <Picture
                                mobile={{
                                    jpeg:
                                        secondExperience?.mainPhoto.mobile.jpeg,
                                    webp:
                                        secondExperience?.mainPhoto.mobile.webp,
                                }}
                                desktop={{
                                    jpeg:
                                        secondExperience?.mainPhoto.desktop
                                            .jpeg,
                                    webp:
                                        secondExperience?.mainPhoto.desktop
                                            .webp,
                                    media: "(min-width: 1024px)",
                                }}
                                alt="client"
                                classNameImg={cx("main-photo")}
                            />
                        </div>
                    </div>
                </div>
                {images && (
                    <div
                        className={cx("container-images", {
                            slideToLeftTeeth:
                                showNextExp === ExperienceType.FIRST,
                            slideToRightTeeth:
                                showNextExp === ExperienceType.SECOND,
                        })}
                    >
                        {images.map((image, index) => (
                            <div className={cx("image")} key={index}>
                                <Picture
                                    mobile={{
                                        jpeg: image.mobile.jpeg,
                                        webp: image.mobile.webp,
                                    }}
                                    desktop={{
                                        jpeg: image.desktop.jpeg,
                                        webp: image.desktop.webp,
                                        media: "(min-width: 1024px)",
                                    }}
                                    alt="teeth"
                                />
                            </div>
                        ))}
                    </div>
                )}
            </div>

            <div className={cx("wrapper-text")}>
                <div className={cx("slide-container")}>
                    <div
                        className={cx("slide-track", {
                            slideToRight: showNextExp === ExperienceType.FIRST,
                            slideToLeft: showNextExp === ExperienceType.SECOND,
                        })}
                    >
                        <div className={cx("container-text")}>
                            <p className={cx("subtitle")}>
                                {secondExperience?.subtitle}
                            </p>
                            <p className={cx("tooltip")}>
                                {secondExperience?.tooltip}
                            </p>
                            {treatmentPlan && (
                                <p className={cx("treatment")}>
                                    {secondExperience?.treatmentPlan}
                                </p>
                            )}
                            <p className={cx("description")}>
                                {secondExperience?.description}
                            </p>
                            <button
                                type="button"
                                onClick={handleNextExp}
                                className={cx("button-next")}
                            >
                                {secondExperience?.titleNextOpinion}
                            </button>
                        </div>
                        <div className={cx("container-text")}>
                            <p className={cx("subtitle")}>{subtitle}</p>
                            <p className={cx("tooltip")}>{tooltip}</p>
                            {treatmentPlan && (
                                <p className={cx("treatment")}>
                                    {treatmentPlan}
                                </p>
                            )}
                            <p className={cx("description")}>{description}</p>
                            {!!secondExperience && titleNextOpinion && (
                                <button
                                    type="button"
                                    onClick={handleNextExp}
                                    className={cx("button-next")}
                                >
                                    {titleNextOpinion}
                                </button>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
