import { FC } from "react";
import classNames from "classnames/bind";

import { TextWithIcon } from "common/components/new/TextWithIcon";
import Scan3DIcon from "common/components/Icons/icons/48/scan3D.svg";
import HouseMonoIcon from "common/components/Icons/icons/48/house-mono.svg";
import {
    Heading,
    HeadingTag,
    HeadingTheme,
    HeadingType,
} from "common/components/new/Heading";

import { TeenAdvantagesProps } from "./TeenAdvantages.types";
import styles from "./TeenAdvantages.module.scss";

const cx = classNames.bind(styles);

export const TeenAdvantages: FC<TeenAdvantagesProps> = ({
    title,
    content,
    className,
}) => {
    const renderIcon = (iconName?: string) => {
        switch (iconName) {
            case "scan3D48":
                return <Scan3DIcon />;
            case "houseMono48":
                return <HouseMonoIcon />;
            default:
                return null;
        }
    };

    return (
        <div className={cx("root", className)}>
            <div className={cx("adolescent-treatment__content")}>
                <Heading
                    className={cx("adolescent-treatment__title")}
                    tag={HeadingTag.H3}
                    type={HeadingType.M}
                    theme={HeadingTheme.White}
                >
                    {title}
                </Heading>

                {content.map((item) => {
                    return (
                        <TextWithIcon
                            {...item}
                            icon={renderIcon(item.icon)}
                            className={cx("adolescent-treatment__content-item")}
                            iconClassName={cx(
                                "adolescent-treatment__content-icon"
                            )}
                            textClassName={cx(
                                "adolescent-treatment__content-text"
                            )}
                            key={item.icon}
                        />
                    );
                })}
            </div>
        </div>
    );
};
