import { FC, useState } from "react";
import classNames from "classnames/bind";

import { Container } from "common/components/Container";

import { BracketsProps } from "./Brackets.types";
import { Experience } from "./components/Experience";
import styles from "./Brackets.module.scss";

const cx = classNames.bind(styles);

export enum ExperienceType {
    FIRST = "first",
    SECOND = "second",
}

export const Brackets: FC<BracketsProps> = ({
    firstExperience,
    secondExperience,
}) => {
    const [showNextExp, setShowNextExp] = useState<ExperienceType | null>(null);

    const handleNextExp = () => {
        let value = null;

        if (showNextExp === null) {
            value = ExperienceType.FIRST;
        } else if (showNextExp === ExperienceType.FIRST) {
            value = ExperienceType.SECOND;
        } else if (showNextExp === ExperienceType.SECOND) {
            value = ExperienceType.FIRST;
        }

        setShowNextExp(value);
    };

    return (
        <section className={cx("root")}>
            <Container className={cx("container")}>
                <Experience
                    handleNextExp={handleNextExp}
                    showNextExp={showNextExp}
                    firstExperience={firstExperience}
                    secondExperience={secondExperience}
                />
            </Container>
        </section>
    );
};
