import { FC } from "react";
import classNames from "classnames/bind";

import { CountryVersion } from "common/constants/enum";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { Button, ButtonSize } from "common/components/new/Button";
import { Picture } from "common/components/Picture";
import { Text, TextTag } from "common/components/Text";

import { TeensClinicVisitProps } from "./TeensClinicVisit.types";
import styles from "./TeensClinicVisit.module.scss";

const cx = classNames.bind(styles);

export const TeensClinicVisit: FC<TeensClinicVisitProps> = ({
    title,
    description,
    ctaButton,
    poster_big,
    poster_small,
    className,
    onButtonClick,
    buttonLink,
    version,
}) => {
    const buttonAction = onButtonClick
        ? { onClick: onButtonClick }
        : { href: buttonLink };

    return (
        <section className={cx("root", className)}>
            <div className={cx("content")}>
                <Heading
                    className={cx("title", {
                        title_small: version === CountryVersion.PORTUGAL,
                    })}
                    tag={HeadingTag.H2}
                >
                    {title}
                </Heading>
                <div className={cx("mobile-galery")}>
                    <Picture
                        className={cx("mobile-image", "mobile-image_1")}
                        mobile={poster_small.mobile}
                    />
                    <Picture
                        className={cx("mobile-image", "mobile-image_2")}
                        mobile={poster_big.mobile}
                    />
                </div>
                <Text className={cx("description")} tag={TextTag.P}>
                    {description}
                </Text>
                <Button
                    className={cx("button")}
                    size={ButtonSize.M}
                    {...buttonAction}
                >
                    {ctaButton}
                </Button>
            </div>
            <div className={cx("desktop-galery")}>
                <Picture
                    className={cx("desktop-image", "desktop-image_1")}
                    desktop={poster_small.desktop}
                />
                <div className={cx("desktop-image", "desktop-image_2")}>
                    <Picture desktop={poster_big.desktop} />
                    <div className={cx("wave-background")} />
                </div>
            </div>
        </section>
    );
};
